.nav {
  display: flex;
  align-self: end;
  font-weight: 400;
  font-size: 20px;
}
.nav__list {
  display: flex;
  align-items: center;
  gap: 60px;
}
.nav__item-blue {
  padding: 12px 18px;
  border-radius: 8px;
  background-color: #00A4D7;
}
.nav__link {
  color: #000;
}
.nav__link-white {
  color: #fff;
}